.popularRibbion {
    position: absolute;
    right: 0px;
    top: -1px;
    max-height: 20px;
}

.inappFoot {
    border-top: 0.7px solid #DCE0E3;
    padding: 20px 60px;
    margin-top: 30px;
}

.inappFoot span {
    color: #637079;
}

.inappFoot span+span {
    text-decoration: underline;
    cursor: pointer;
}

.inappFoot span+span:hover {
    text-decoration: none;
}

.inappBody {
    padding: 50px 60px;
}

.inappBody .inappTitle {
    margin-bottom: 25px;
}

.inappBody .inappTitle strong {
    font-size: 18px;
    font-weight: 600;
    color: #202223;
    display: block;
}

.inappBody .inappTitle span {
    font-size: 14px;
    font-weight: 500;
    color: #637079;
}

.inappDesc textarea {
    resize: none;
}

.inappUpload label,
.inappDesc label {
    font-size: 14px;
    font-weight: 500;
    color: #1A232F;
}

.inappUpload .form-group span {
    padding: 16px 20px;
    border: 1px dashed #626B76;
    border-radius: 4px;
    color: #1A232F;
}

.inappUpload .form-group span:hover {
    cursor: pointer;
    opacity: 0.9;
}

.inappUpload i {
    font-size: 12px;
    font-weight: 400;
    color: #637079;
    font-style: normal;
}

.inappUpload .fc-uploaded-inn {
    position: relative;
    display: inline-block;
}

.inappUpload .fc-uploaded-inn .close {
    position: absolute;
    right: -10px;
    top: -10px;
    cursor: pointer;
}

.fc-uploaded {
    padding: 10px 20px;
    border: 1px dashed #626B76;
    border-radius: 4px;
    color: #1A232F;
    text-align: center;
}

.inappUpload .fc-uploaded-inn>img,
.inappUpload .fc-uploaded-inn>video {
    height: 32px;
    max-height: 32px;
    vertical-align: middle;
}

/*  */
.planTitle {
    margin-bottom: 20px;
}

.planTitle img {
    max-height: 50px;
}

.planTitle .planTitleinn strong {
    font-size: 21px;
    font-weight: 600;
    color: #2B2B2B;
    margin-bottom: 5px;
    display: block;
}

.planTitle .planTitleinn p {
    font-size: 14px;
    font-weight: 400;
    color: #545454;
}

.planAll>div {
    width: 31%;
}

.popup-inn .popupClose {
    position: absolute;
    right: -40px;
    top: -30px;
    cursor: pointer;
}

.popup-inn .inappTitle .popupClose {
    right: 20px;
    top: 15px;
}

.popup-wrap .seeAllplan {
    padding-top: 20px;
}

.popup-wrap .seeAllplan span {
    font-weight: 400;
    font-size: 14px;
    color: #545454;
    text-decoration-line: underline;
}

.popup-wrap .seeAllplan span:hover {
    text-decoration: none;
}

/* Feature Plan */
.planFeatures ul {
    border: 1px solid #E2E2E2;
    border-bottom: 0;
}

.planFeatures ul,
.planFree ul,
.planLite ul {
    border-radius: 8px;
}

.planFeatures ul li,
.planFree ul li,
.planLite ul li {
    padding: 10px 20px;
    color: #545454;
    font-size: 14px;
    font-weight: 500;
    list-style: none;
}

.modal.modal-lg .modal-dialog {
    max-width: 800px;
}

.planFeatures ul li {
    font-weight: 400;
    padding-top: 11px;
    padding-bottom: 11px;
    border-bottom: 1px solid #E2E2E2;
}

.planFeatures ul li:last-child {
    border-radius: 0 8px 8px 0;
}

.planFeatures ul li span,
.planFree ul li span {
    color: #949494;
    font-weight: 500;
    font-size: 16px;
}

/* Free Plan */
.planFree ul {
    background: #F2F5FB;
}

.planFree ul li {
    /* padding-top: 15px;
    padding-bottom: 15px; */
}

.planFree ul li span {
    color: #545454;
}

.planFree ul li:first-child {
    background: #DEE3EB;
    border-radius: 8px 8px 0 0;
}

/* Lite Plan */
.planLite ul {
    box-shadow: 0px 0px 24px 0px #7A859C40;
    background-color: #fff;
}

.planLite ul li {
    padding-top: 12px;
    padding-bottom: 12px;
}

.planLite ul li:first-child {
    background-color: #FF4051;
    border-radius: 8px 8px 0 0;
}

.te-theme .planLite ul li:first-child {
    background-color: #4179ff;
}

.planLite ul li:first-child span {
    color: #fff;
    margin-top: 5px;
}

.planLite ul li:first-child img {
    max-height: 28px;
}

.planLite button {
    margin-bottom: 10px;
}